import React ,{useContext} from 'react'
import userContext from '../userContext'
import store from '../store'
import { clear } from '../actions'

export default function InformationsComponent() {
    const{finalPrice,setFinalPrice,firstName,setFirstName,lastName,
        setLastName,email,setEmail,phoneNumber,setPhoneNumber,city,setCity,
        street,setStreet,house,setHouse,takeAwayOrShipping,setTakeAwayOrShipping
    ,orderNumber,setOrderNumber} = useContext(userContext)



        const informationsDone = ()=>
        {
            if(firstName === '' || lastName === '' || email === '' || phoneNumber === '' ||city === '' || street === '' || house === ''||takeAwayOrShipping ==='')
            {
                return alert('      *')
            }
            else if(email.indexOf('@') === -1)
            {
                return alert('      ')
            }
            else{
                fetch('/BuyNow', 
                {
                    headers:{
                        "Content-Type": "application/json"
                    },
                    method:'post',
                    body:JSON.stringify({
                        firstName:firstName,
                        lastName:lastName,
                        email:email,
                        phoneNumber:phoneNumber,
                        city:city,
                        street:street,
                        house:house,
                        orderNumber:orderNumber,
                        finalPrice:finalPrice,
                        setTakeAwayOrShipping:setTakeAwayOrShipping,
                        cart:store.getState().cartReducer
                    })
                }).then((res)=>{return res.json()})
                .then((data)=>
                {
                  if(data === 'done')
                  {
                    setFirstName('')
                    setLastName('')
                    setEmail('')
                    setPhoneNumber('')
                    setCity('')
                    setHouse('')
                    setStreet('')
                    setFinalPrice(0)
                    setOrderNumber(0)
                    setTakeAwayOrShipping('')
                    store.dispatch(clear())
                    // setName('')
                    // setPhoneNumber('')
                    // setCity('')
                    // setEmail('')
                    // setNotes('')
                    // setTakeAwayOrShipping('Take Away')
                    // setLocation(null)
                    // setCart([])
                    // SetFinalPrice(0)
                    // nav('/Notes')
                    // setTimeout(() =>nav("/"), 1500)
                  }
                  else
                  {
                    alert('         ')
                  }
                }).catch((err)=>{return err})
            }
        }


  return (
    <div className='InformationsDiv'>
    <h1 style={{textAlign:'right'}}>  
        <br />
        <br />
        <span style={{color:'wheat'}}>
                 <br />  
            <br />
                
        </span>
    </h1>
    <div className='FirstNameLastName'>
        <label className='informationsLabel' htmlFor="FirstName">
            <h4><span>*</span>  </h4>
            <input id='FirstName' value={firstName} onChange={(e)=>{setFirstName(e.target.value)}} placeholder=' ' type="text" />
        </label>
        <label className='informationsLabel' htmlFor="LastName">
            <h4><span>*</span> </h4>
            <input id='LastName' value={lastName} onChange={(e)=>{setLastName(e.target.value)}} placeholder=' ' type="text" />
        </label>
    </div>
    <div className='theRemainingInformations'>
        <label className='theRemainingInformationsLabel' htmlFor="email">
            <h4><span>*</span> </h4>
            <input id='email' autoComplete='email' value={email} onChange={(e)=>{setEmail(e.target.value)}} placeholder=' ' type="email" />
        </label>
        <label className='theRemainingInformationsLabel' htmlFor="phonenumber">
            <h4><span>*</span> </h4>
            <input id='phonenumber' value={phoneNumber} onChange={(e)=>{setPhoneNumber(e.target.value)}} placeholder=' ' type="number" />
        </label>
        <label className='theRemainingInformationsLabel' htmlFor="city">
            <h4><span>*</span></h4>
            <input id='city' value={city} onChange={(e)=>{setCity(e.target.value)}} placeholder='' type="text" />
        </label>
        <label className='theRemainingInformationsLabel' htmlFor="street">
            <h4><span>*</span>/ </h4>
            <input id='street' value={street} onChange={(e)=>{setStreet(e.target.value)}} placeholder='/ ' type="text" />
        </label>
        <label className='theRemainingInformationsLabel' htmlFor="house">
            <h4><span>*</span> </h4>
            <input id='house' value={house} onChange={(e)=>{setHouse(e.target.value)}} placeholder=' ' type="number" />
        </label>
        <label className='theRemainingInformationsLabel' htmlFor="takeAwayOrShipping">
        <h4><span>*</span> </h4>
        <select value={takeAwayOrShipping} onChange={(e)=>{setTakeAwayOrShipping(e.target.value)}} name="takeAwayOrShipping" id="takeAwayOrShipping">
            <option value="0"></option>
            <option value="Shipping">   </option>
            <option value="TakeAway">  </option>
        </select>
        </label>
        <button className='SendInformationsButton' onClick={informationsDone}></button>
    </div>
</div>
  )
}
